<template>
    <div id="edit-employee">
        <h3>Edit Employee</h3>
         <div class="row">
            <form @submit.prevent="updateEmployee" class="col s12">
            <div class="row">
                <div class="input-field col s12">
                <input type="text" disabled v-model="employee_id" placeholder="Employee ID#" required>
                <!-- <label>Employee ID#</label> -->
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                <input type="text" v-model="name"  placeholder="Name" required>
                <!-- <label>Name</label> -->
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                <input type="text" v-model="dept" placeholder="Department" required>
                <!-- <label>Department</label> -->
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                <input type="text" v-model="position" placeholder="Position" required>
                <!-- <label>Position</label> -->
                </div>
            </div>
            <button type="submit" class="btn">Submit</button>
            <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-employee',
    data (){
        return{
            employee_id: null,
            dept: null,
            position: null,
            name: null
        }
    },
    created(){
        this.fetchData();
    },
    watch:{
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                this.employee_id = doc.data().employee_id
                this.name = doc.data().name
                this.dept = doc.data().dept
                this.position = doc.data().position
                })
            })
        },
        updateEmployee(){
            db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                    doc.ref.update({
                        employee_id: this.employee_id,
                        name: this.name,
                        dept: this.dept,
                        position: this.position
                    })
                    .then(() => {
                        this.$router.push({ name: 'view-employee', params: { employee_id: this.employee_id }})
                    });
                })
            })
        }
    }
}
</script>


